@import url(https://fonts.googleapis.com/css2?family=Caveat&display=swap);
*{
    box-sizing: border-box;
}

.remove-padding {
    padding: 0px;
}

body{
    background-image: url(/static/media/moviebg3.298c67fd.jpg);
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
}

.card{
    margin: 15px auto 15px;
    width: 19rem;
    color:  rgba(69, 123, 157, 0.9);
    box-shadow: 0px 0px 20px 1px black !important;
}

.card-title{
    text-align: center;
    color: white;
}

.row{
    width: 100%;
    display: flex;
}
.background {
    background: linear-gradient(rgba(69, 123, 157, 0.7),rgba(69, 123, 157, 0.1));
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 20vh;
    padding: 10px;
}

.search {
    height: 38px;
    width: 350px;
}

.clear-margin-top {
    margin-top: -16px;
}

.btn2{
    background-color: rgba(255,0,0,0.9);
    color: white;
}

.btn2:hover{
    background-color: rgba(255,0,0,0.4);
}
.info{
    border-radius: 20px;
    box-shadow: 0 0 10px 1px darkblue inset;
    font-size: 1.3rem;
    margin: 2rem auto 1rem;
    background-color: rgba(69, 123, 157, 0.7);
    color: white;
    line-height: 1.9rem;
}

h5{
    padding-top: 5px;
}

.list{
    padding-top: 10px;
    padding-bottom: 10px;
}

.list li{
    margin: 1rem 0;
}
.container{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 15rem;
}

.text{
    color: #C5EFCB;
    font-family: 'Caveat', cursive;
    font-size: 7rem;
}

.addShadow{
    text-shadow: 2px 2px black !important;
}

.btn1{
    background-color: rgba(16, 194, 16, 0.829);
    color: white;
}

.btn1:hover{
    background-color: rgba(16, 194, 16, 0.3);
}
